     float: right;
 }

 .login-form-button {
     width: 100%;
 }

 .user-drawer .ant-drawer-header{
     padding: 0
 }

.traffic-wrap{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin: 20px auto;
    height: 600px;
    -webkit-justify-content: center;
            justify-content: center;
    border-bottom: 5px solid;
}

.traffic-light {
    width: 100px;
    border: 5px solid;
    border-radius: 20px;
    background: black;
}

.traffic-light div {
    width: 90px;
    height: 90px;
    margin: 10px 0;
    border: 5px solid;
    border-radius: 100%;
    line-height: 80px;
    text-align: center;
    font-size: 50px;
}

.red-light {
    color: #5f4444;
    border-color: red;
    background: red;
    opacity: 0.5;
}

.yellow-light {
    color: orange;
    border-color: orange;
    background: yellow;
    opacity: 0.5;
}

.green-light {
    color: lightgreen;
    border-color: lightgreen;
    background: green;
    opacity: 0.5;
}

.hoz-line {
    width: 120px;
    height: 20px;
    background: black;
    margin: 175px 0;
    border-radius: 0 5px 0 0;
}
.vet-line {
    width: 20px;
    height: 410px;
    background: black;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin: 0 0 0 -20px;
}
